@use "../../../../../indexCompile" as *

$aviationExhaustColour: rgb(10, 10, 10)


.timelineAviationAnimImgWrapper, .timelineAviationAnimExhaustL, .timelineAviationAnimExhaustR 
    width: 100vw
    height: 150vh
    transform: translateY(120vh)
    animation: timelineAviationAnimP1 1.75s cubic-bezier(.59,.12,.49,.74)
    /* transform: translateY(50vh) */
    position: absolute
    animation-fill-mode: forwards
    z-index: 0
    opacity: 1


.timelineAviationAnimImgWrapper 
    background: linear-gradient($aviationExhaustColour 10%, $black 40%, $black 60%, rgba($black, 0) 100%)


.timelineAviationAnimImgWrapper::before, .timelineAviationAnimImgWrapper::after 
    content: ""
    width: 100vw
    height: 40vh
    background: transparent
    border-radius: 50%
    position: absolute
    border-bottom: calc(max(0.5vw, 5px)) solid $orange


.timelineAviationAnimImgWrapper::before 
    transform: translate(calc(-50vw - 0.5vw), calc(-37.5vh - 1.25vw))
    box-shadow: -5vh 12.5vh 0px 12.5vh $aviationExhaustColour
    clip-path: inset(20vh -1vw 0px 0px)


.timelineAviationAnimImgWrapper::after 
    transform: translate(calc(50vw + 0.5vw), calc(-37.5vh - 1.25vw))
    box-shadow: 5vh 12.5vh 0px 12.5vh $aviationExhaustColour
    clip-path: inset(20vh 0px 0px -1vw)


.timelineAviationAnimExhaustL::before, .timelineAviationAnimExhaustL::after, .timelineAviationAnimExhaustR::before, .timelineAviationAnimExhaustR::after 
    content: ""
    width: 50vw
    height: 400vh
    background: transparent
    border-radius: 50%
    position: absolute
    /* border-bottom: calc(max(0.1vw, 2px)) solid $orangeTranslucent) */
    margin-top: calc(-200vh + 1vh)


.timelineAviationAnimExhaustL 
    margin-left: -0.5vw


.timelineAviationAnimExhaustR 
    margin-left: 0.5vw


.timelineAviationAnimExhaustL::before, .timelineAviationAnimExhaustR::before 
    transform: translate(calc(0vw - 0vw), calc(-37.5vh - 1.25vw))
    box-shadow: 20vh 12.5vh 0px 12.5vh #f06f4f33
    clip-path: inset(220vh -0vw 1vh 25vw)
    animation: timelineAviationAnimExhaustColour1 1.75s cubic-bezier(.59,.12,.49,.74)


.timelineAviationAnimExhaustL::after, .timelineAviationAnimExhaustR::after 
    transform: translate(calc(50vw + 0vw), calc(-37.5vh - 1.25vw))
    box-shadow: -20vh 12.5vh 0px 12.5vh #f06f4f33
    clip-path: inset(220vh 25vw 1vh -0vw)
    animation: timelineAviationAnimExhaustColour2 1.75s cubic-bezier(.59,.12,.49,.74)


.timelineAviationAnimExhaustL::after 
    height: 500vh
    margin-top: calc(-250vh + 1vh)
    clip-path: inset(270vh 25vw 51vh -0vw)


.timelineAviationAnimExhaustR::before 
    height: 500vh
    margin-top: calc(-250vh + 1vh)
    clip-path: inset(270vh -0vw 51vh 25vw)


.timelineAviationAnimImg 
    filter: invert(69%) sepia(64%) saturate(4973%) hue-rotate(331deg) brightness(102%) contrast(88%)
    position: absolute
    transform: translate(-50%, 5vh)
    left: 50%
    margin-top: calc(-22.5vh - 2.5vw)
    /* animation: timelineAviationAnim 1.25s ease-in */
    aspect-ratio: 1
    width: 2.5vw
    min-width: 36px
    height: auto
    z-index: 2
